import React, {useRef,useEffect}from 'react'; // Import the Component component from React


 
function Hero(props) {
  const characterRef = useRef(null);

  useEffect(() => {


    const observerConfig = {
      rootMargin: `-50% 0px -50% 0px `,
      };
      

    const handleIntersection = function(entries) {
      entries.forEach((entry) => {
        if (entry.target.id !== props.activeCharacter && entry.isIntersecting) {
          props.setActiveCharacter(entry.target.id);
          console.log(entry.target.id);
        }
      });
    };
    
    const observer = new IntersectionObserver(handleIntersection,observerConfig);
    observer.observe(characterRef.current);
   
    
  }, [props.activeCharacter, props.setActiveCharacter, characterRef]);

  return (
<div className="row section-lg no-gutters " id ="Hero" ref ={characterRef} >
            <div className= {'col hero-img colorthis order-last '} > <img src= {props.img}  alt="Portfolio "></img> </div>
            <div className= {'col hero-content d-flex align-items-center  justify-content-center'} > 
            
            {props.children}
            
            </div>
         
          </div>
  );
}
 
export default Hero;