import React from 'react'; // Import the Component component from React


 
function Block(props) {

  const descript = [];
  props.content.description.forEach(item=> descript.push(<p>{item}</p>))
  
  return (
   
    
    <div className=" experience row mb-5">
        <div className ="col-4 "> 
            <strong className= {`py-1 ${props.orientation ? "" : "text-right" } `} > {props.content.position} </strong> 
            <p className = {` ${props.orientation ? "" : "text-right"  }` }> {props.content.date}</p>
        </div>
        <div className = {`col-8   text-left  ${props.orientation ? "order-first" : " " } ` } style ={{backgroundColor:props.color}}> 
        <strong className="py-2" > {`${props.content.employer}, ${props.content.location}`}</strong>
        
          {
              descript
          }



         

        
        
        </div>

    </div>

 
  );
}
 
export default Block;