import React from 'react'; // Import the Component component from React


 
function Nav(props) {


  

  return (

    <div className="side-nav  text-right">

  
   
    <nav className="nav flex-column py-5 overflow-hidden ">
    <a href="#Hero" className= {`nav-item nav-link  text-uppercase ${"Hero" === props.activeCharacter? "active": ""}` }>  <b>Arjun </b> </a>
    
   
    {props.items.map( (value,index) => {
      
      const activeClass = (value === props.activeCharacter? "active": "");
      return  <a href= {`#${value}`} className= {`nav-item nav-link ${activeClass} color${index +1}` } >{value}</a>
    }
    )}

  
    </nav>
    </div>


  )}
 
export default Nav;