import React,{useEffect,useRef} from 'react'; // Import the Component component from React




function Section(props) {

  
  const characterRef = useRef(null);


  
  

 

    useEffect(() => {


      const observerConfig = {
        rootMargin: `-50% 0px -50% 0px `,
        };
        

      const handleIntersection = function(entries) {
        entries.forEach((entry) => {
          if (entry.target.id !== props.activeCharacter && entry.isIntersecting) {
            props.setActiveCharacter(entry.target.id);
            console.log(entry.target.id);
          }
        });
      };
      
      const observer = new IntersectionObserver(handleIntersection,observerConfig);
      observer.observe(characterRef.current);
     
      
    }, [props.activeCharacter, props.setActiveCharacter, characterRef]);


  return (
<div className="row section-lg min-h-5  section" id = {props.title} ref ={characterRef} >
            <div className= {'col-4 content  section-title d-flex  justify-content-center' + (props.direction==="left"? ' order-last': ' order-first') } style ={{ backgroundColor: props.pcolor }} ><h1 className="display-3 mt-c "> {props.title}</h1> </div>
            <div className= {'col-8 content section-content py-5 px-3  justify-content-center '}  style ={{ backgroundColor: props.scolor }}> {props.children} </div>
          </div>
  );
}
 
export default Section;