import React,{useState} from 'react'; // Import the Component component from React


import block from './images/block.png'
import can_collect from './images/can_collect.png'
import in_res from './images/in_res.PNG'
import live_laugh from './images/live_laugh.PNG'
import mlcontrol from './images/mlcontrol.png'

 
function Card(props) {

  const details = props.project;


const tags =[];
const description =[];
const links =[];

details.tags.forEach( element => tags.push(<li className="list-inline-item tag"> {element}</li>));
details.description.forEach( element => description.push(  <p>{element}</p>));

if  ('Links' in details){
  details.Links.forEach( (element,index) => 
{

  if (index%2==0){
    links.push(<li className="list-inline-item"> <a href={details.Links[index +1]} target= "_blank">{element}</a></li>)

  }


});

}

const image_src = [];


const   [isCollapsed,setCollapse] = useState(true);  

function toggleCard(e){
    e.preventDefault();
    console.log(isCollapsed);
    setCollapse(isCollapsed?false:true);
}



  return (


  
        <div className= {`mx-3  row project-container ${ isCollapsed? "collapsed": "expanded"}`} >

        

        <div className =" col-5  ">
          <div className = "project-thumbnail  d-flex justify-content-center text-center  " style={{backgroundImage:`url(./images/${details.image})`, backgroundColor:details.alt_color}}  >

          <h2 > {details.alt}</h2>

          </div>
          <div className="project-tags hidden-content ">
            
            <ul className = "list-inline" >
                   {tags}
                    
                </ul>

          </div>

          <ul className = "list-unstyled card-specs hidden-content">
            <li><p> Project Type : {details.type}</p> </li>
            <li><p> Date : {details.date}</p> </li>

          </ul>
          <ul className ="list-inline project-links hidden-content">
            {links}
          </ul>

         

            </div>


            <div className = "col-7 px-3 py-4" >
                <h3 className = "pb-3">{details.name}</h3>
                <p>{details.intro}
                </p>
                
                <div className=" hidden-content">
               
                {description}
                
                </div>

                <a className="toggle-container" href="#" onClick={toggleCard}> { isCollapsed? "Read More": "Read Less"}</a>

                
               
                

            </div>

        </div>

   

    
    
  );
}
 
export default Card;