import React, { useState,useRef } from 'react'; // Import the Component component from React

function Fold(props) {

  const   [isActive,setActive] = useState("sleepy");
  const     [Height, setHeight] = useState("0px");
  const     [Direction, setDirection] = useState("down");

  const fold_content = useRef(null);

  function toggleFold(){
    setActive(isActive === "sleepy" ? "active": "sleepy");
    setHeight(
      isActive === "active" ? `0px` : `${fold_content.current.scrollHeight}px`
    );
    setDirection(
      isActive === "active" ? `down` : `up`
    );
   
    
  }

  const list_items =[];


 

    props.item.forEach(item => {

      list_items.push(<li > {item}</li>)
    })

  
  return (
  <div className= {`fold ${isActive}`} data-expand="hide">
  <div className ="fold-head col-12" onClick ={toggleFold} >
      <h5 className="float-left "> {props.header}</h5>
      <span className ="float-right"><i className={`arrow acc-icon ${Direction}`}>  </i> </span>
  </div>
  
  <div className ="fold-content col-12 pt-3" style={{ maxHeight: `${Height}` }} ref={fold_content} > 
    <ul className="skill-list py-4" style = {{backgroundColor: props.color}}> 
    {list_items}
    </ul>
  
  </div>

</div>);
}
 



 
export default Fold;